import React, { Component } from 'react'

export default class Skills extends Component {
    render() {
        return (
            <div>
                <section className="colorlib-skills" data-section="skills">
                    <div className="colorlib-narrow-content" style={{ paddingBottom: "100px" }}>
                        <div className="row">
                            <div className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box fadeInLeft animated" data-animate-effect="fadeInLeft">
                                <span className="heading-meta">My Specialty</span>
                                <h2 className="colorlib-heading animate-box fadeInUp animated">My Skills</h2>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-12 animate-box fadeInLeft animated" data-animate-effect="fadeInLeft" style={{paddingBottom: "50px"}}>
                                <h4 className="colorlib-heading">My Skills are my strength, So these are the few technologies where i have got an opportunity to work on in the past Around Three years of time span.</h4>
                            </div><br/>
                            <br/>
                            <div className="col-md-6 animate-box fadeInLeft animated" data-animate-effect="fadeInLeft">
                                <div className="progress-wrap">
                                    <h3>C# .Net</h3>
                                    <div className="progress">
                                        <div className="progress-bar color-1" role="progressbar"
                                            aria-valuenow="100" aria-valuemin="0" aria-valuemax="100" style={{ width: "100%" }}>
                                            {/* <span>75%</span> */}
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-6 animate-box fadeInRight animated" data-animate-effect="fadeInRight">
                                <div className="progress-wrap">
                                    <h3>ReactJs</h3>
                                    <div className="progress">
                                        <div className="progress-bar color-2" role="progressbar"
                                            aria-valuenow="100" aria-valuemin="0" aria-valuemax="100" style={{ width: "100%" }}>
                                            {/* <span>60%</span> */}
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-6 animate-box fadeInLeft animated" data-animate-effect="fadeInLeft">
                                <div className="progress-wrap">
                                    <h3>JavaScript and jQuery</h3>
                                    <div className="progress">
                                        <div className="progress-bar color-3" role="progressbar" aria-valuenow="100"
                                            aria-valuemin="0" aria-valuemax="100" style={{ width: "100%" }}>
                                            {/* <span>85%</span> */}
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-6 animate-box fadeInRight animated" data-animate-effect="fadeInRight">
                                <div className="progress-wrap">
                                    <h3>HTML and CSS</h3>
                                    <div className="progress">
                                        <div className="progress-bar color-4" role="progressbar" aria-valuenow="100"
                                            aria-valuemin="0" aria-valuemax="100" style={{ width: "100%" }}>
                                            {/* <span>90%</span> */}
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-6 animate-box fadeInLeft animated" data-animate-effect="fadeInLeft">
                                <div className="progress-wrap">
                                    <h3>C and C++</h3>
                                    <div className="progress">
                                        <div className="progress-bar color-5" role="progressbar"
                                            aria-valuenow="100" aria-valuemin="0" aria-valuemax="100" style={{ width: "100%" }}>
                                            {/* <span>70%</span> */}
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-6 animate-box fadeInRight animated" data-animate-effect="fadeInRight">
                                <div className="progress-wrap">
                                    <h3>SQL Server</h3>
                                    <div className="progress">
                                        <div className="progress-bar color-6" role="progressbar"
                                            aria-valuenow="100" aria-valuemin="0" aria-valuemax="100" style={{ width: "100%" }}>
                                            {/* <span>80%</span> */}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        )
    }
}