import React, { Component } from 'react'

export default class Education extends Component {
    render() {
        return (
            <div>
                <section className="colorlib-education" data-section="education">
                    <div className="colorlib-narrow-content">
                        <div className="row row-bottom-padded-sm animate-box">
                            <div className="col-md-12">
                                <div className="contact-desc">
                                    <span className="heading-meta">Education</span>
                                    <h2 className="colorlib-heading">Education</h2>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-12 animate-box fadeInLeft animated" data-animate-effect="fadeInLeft">
                                    <div className="fancy-collapse-panel">
                                        <div className="panel-group" id="accordion" role="tablist" aria-multiselectable="true">
                                            <div className="panel panel-default">
                                                <div className="panel-heading" role="tab" id="headingOne">
                                                    <h4 className="panel-title">
                                                        <a data-toggle="collapse" data-parent="#accordion" href="#collapseOne"
                                                            aria-expanded="false" aria-controls="collapseOne" className="collapsed">RajaRajeshwari College of Engineering
									            </a>
                                                    </h4>
                                                </div>
                                                <div id="collapseOne" className="panel-collapse collapse" role="tabpanel" aria-labelledby="headingOne"
                                                    aria-expanded="false" style={{ height: "0px" }}>
                                                    <div className="panel-body">
                                                        <ul>
                                                            <li>Till 2015</li>
                                                            <li>B.E in Computer Science Engineering</li>
                                                            <li>Passed with 71%</li>
                                                            <li>Bangalore, Karnataka, India</li>
                                                        </ul>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="panel panel-default">
                                                <div className="panel-heading" role="tab" id="headingTwo">
                                                    <h4 className="panel-title">
                                                        <a className="collapsed" data-toggle="collapse" data-parent="#accordion" href="#collapseTwo"
                                                            aria-expanded="false" aria-controls="collapseTwo">SJR PU College
									            </a>
                                                    </h4>
                                                </div>
                                                <div id="collapseTwo" className="panel-collapse collapse" role="tabpanel" aria-labelledby="headingTwo" aria-expanded="false" style={{ height: "0px" }}>
                                                    <div className="panel-body">
                                                        <ul>
                                                            <li>Till 2011</li>
                                                            <li>Passed with 58%</li>
                                                            <li>Bangalore, Karnataka, India</li>
                                                        </ul>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="panel panel-default">
                                                <div className="panel-heading" role="tab" id="headingThree">
                                                    <h4 className="panel-title">
                                                        <a className="collapsed" data-toggle="collapse" data-parent="#accordion" href="#collapseThree"
                                                            aria-expanded="false" aria-controls="collapseThree">Sun Glory High School
									            </a>
                                                    </h4>
                                                </div>
                                                <div id="collapseThree" className="panel-collapse collapse" role="tabpanel" aria-labelledby="headingThree" aria-expanded="false" style={{ height: "0px" }}>
                                                    <div className="panel-body">
                                                        <ul>
                                                            <li>Till 2009</li>
                                                            <li>Passed with 79%</li>
                                                            <li>Bangalore, Karnataka, India</li>
                                                        </ul> </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        )
    }
}
