import React, { Component } from 'react'

export default class Contact extends Component {
    render() {
        return (
            <div>
                <section className="colorlib-contact" data-section="contact">
                    <div className="colorlib-narrow-content" style={{paddingBottom: "200px"}}>
                        <div className="row">
                            <div className="col-md-12">
                                <div className="row row-bottom-padded-sm animate-box">
                                    <div className="col-md-12">
                                        <div className="contact-desc">
                                            <span className="heading-meta">GET IN TOUCH</span>
                                            <h2 className="colorlib-heading">CONTACT</h2>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-5">
                                <div class="colorlib-feature colorlib-feature-sm animate-box fadeInLeft animated" data-animate-effect="fadeInLeft">
                                    <div class="colorlib-icon">
                                        <i class="icon-map"></i>
                                    </div>
                                    <div class="colorlib-text">
                                        <p><b>#3 2nd Main Road K.p.Agrahara Nethaji Nagar Bangalore-23</b></p>
                                    </div>
                                </div>
                                <div class="colorlib-feature colorlib-feature-sm animate-box fadeInLeft animated" data-animate-effect="fadeInLeft">
                                    <div class="colorlib-icon">
                                        <i class="icon-phone"></i>
                                    </div>
                                    <div class="colorlib-text">
                                        <p><b>9738131785</b></p>
                                    </div>
                                </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        )
    }
}
